    import * as React from "react";
    import { useState, useRef, useEffect } from "react";
    import {
    Settings,
    LogOut,
    Sun,
    Moon,
    Menu,
    X,
    Paperclip,
    Trash2,
    Send,
    Loader2,
    } from "lucide-react";

    const NewMessagePage: React.FC = () => {
    const [theme, setTheme] = useState<"light" | "dark">("light");
    const [open, setOpen] = useState(false);
    const [navOpen, setNavOpen] = useState(false);
    const [message, setMessage] = useState("");
    const [attachments, setAttachments] = useState<File[]>([]);
    const [sending, setSending] = useState(false);
    const menuRef = useRef<HTMLDivElement>(null);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const handleAttach = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files) {
        const newFiles = Array.from(e.target.files);
        setAttachments((prev) => [...prev, ...newFiles]);
        }
    };

    const handleRemove = (index: number) => {
        setAttachments((prev) => prev.filter((_, i) => i !== index));
    };

    const handleSend = () => {
        if (!message.trim() && attachments.length === 0) {
        alert("Please write a message or attach at least one file.");
        return;
        }
        setSending(true);
        setTimeout(() => {
        setSending(false);
        alert("âœ… Message sent successfully!");
        setMessage("");
        setAttachments([]);
        }, 2000);
    };

    useEffect(() => {
        const handleClick = (e: MouseEvent) => {
        if (menuRef.current && !menuRef.current.contains(e.target as Node)) setOpen(false);
        };
        document.addEventListener("click", handleClick);
        return () => document.removeEventListener("click", handleClick);
    }, []);

    return (
        <div className={`app ${theme}`}>
        <style>{`
            body, html { margin: 0; font-family: 'Inter', sans-serif; }
            .app { min-height: 100vh; background: var(--bg); color: var(--text); transition: background .3s, color .3s; overflow-x: hidden;}
            .app.light {
            --bg: #f8fafc; --panel: #ffffff; --border: rgba(0,0,0,0.08);
            --text: #111827; --muted: #6b7280; --accent: #10b981; --accent-bg: rgba(16,185,129,0.1);
            }
            .app.dark {
            --bg: #0f1318; --panel: #0b0f14; --border: rgba(255,255,255,0.08);
            --text: #d1d5db; --muted: #9ca3af; --accent: #10b981; --accent-bg: rgba(16,185,129,0.15);
            }

            header {
            background: var(--panel); border-bottom: 1px solid var(--border);
            padding: 12px 20px; display: flex; align-items: center; justify-content: space-between;
            position: sticky; top: 0; z-index: 30;
            }
            .brand { display: flex; align-items: center; gap: 8px; font-weight: 600; color: var(--text); font-size: 14px;}
            .brand-icon { width: 24px; height: 24px; background: linear-gradient(135deg, #10b981, #059669); border-radius: 3px;}
            .actions { display: flex; align-items: center; gap: 10px;}
            button {
            border: none; border-radius: 5px; font-size: 13px; padding: 7px 12px; cursor: pointer;
            background: var(--panel); color: var(--text); display: flex; align-items: center; gap: 6px; transition: all .2s;
            }
            button:hover { background: var(--accent-bg); }
            .primary-btn { background: var(--accent); color: #072c21; font-weight: 500;}

            main { max-width: 900px; margin: 30px auto; padding: 0 20px; display: flex; flex-direction: column; gap: 20px; }

            .message-card {
            background: var(--panel);
            border: 1px solid var(--border);
            border-radius: 10px;
            padding: 20px;
            }

            textarea {
            width: 100%;
            border: 1px solid var(--border);
            background: var(--panel);
            color: var(--text);
            font-size: 14px;
            border-radius: 8px;
            padding: 10px;
            min-height: 120px;
            resize: vertical;
            outline: none;
            }
            textarea:focus {
            border-color: var(--accent);
            box-shadow: 0 0 0 2px var(--accent-bg);
            }

            .attachments {
            margin-top: 15px;
            display: flex;
            flex-wrap: wrap;
            gap: 10px;
            }
            .file-item {
            background: var(--accent-bg);
            border: 1px solid var(--border);
            border-radius: 8px;
            padding: 6px 10px;
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 13px;
            color: var(--text);
            }
            .file-item img {
            width: 30px;
            height: 30px;
            border-radius: 5px;
            object-fit: cover;
            }
            .file-remove {
            background: none;
            color: var(--muted);
            padding: 0;
            border: none;
            cursor: pointer;
            }
            .file-remove:hover {
            color: red;
            }

            .message-actions {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-top: 20px;
            border-top: 1px solid var(--border);
            padding-top: 15px;
            }

            .spinner {
            animation: spin 1s linear infinite;
            width: 16px;
            height: 16px;
            flex-shrink: 0;
            }
            @keyframes spin { from { transform: rotate(0deg);} to { transform: rotate(360deg);} }

            footer { text-align: center; color: var(--muted); font-size: 12px; margin: 40px 0 20px; }

            .dropdown { position: relative; }
            .menu {
            position: absolute; top: 110%; right: 0; background: var(--panel);
            border: 1px solid var(--border); border-radius: 8px;
            box-shadow: 0 4px 10px rgba(0,0,0,0.2);
            width: 160px; overflow: hidden; z-index: 50; display: none;
            }
            .menu.open { display: block; }
            .menu button { width: 100%; justify-content: flex-start; background: none; border-radius: 0; }
            .menu button:hover { background: var(--accent-bg); }
            .menu hr { border: none; border-top: 1px solid var(--border); margin: 4px 0; }
        `}</style>

        {/* HEADER */}
        <header>
            <div className="brand">
            <button onClick={() => setNavOpen(!navOpen)} style={{ background: "none", border: "none", cursor: "pointer" }}>
                {navOpen ? <X size={18} /> : <Menu size={18} />}
            </button>
            <div className="brand-icon" />
            New Message
            </div>
            <div className="actions">
            <div className="dropdown" ref={menuRef}>
                <button onClick={() => setOpen(!open)}>
                <Settings size={14} /> Settings
                </button>
                <div className={`menu ${open ? "open" : ""}`}>
                <button onClick={() => alert("Profile clicked")}>Profile</button>
                <button onClick={() => alert("Preferences clicked")}>Preferences</button>
                <hr />
                <button onClick={() => setTheme(theme === "light" ? "dark" : "light")}>
                    {theme === "light" ? <Moon size={14} /> : <Sun size={14} />} Theme: {theme === "light" ? "Dark" : "Light"}
                </button>
                </div>
            </div>
            <button className="primary-btn">
                <LogOut size={14} /> Sign out
            </button>
            </div>
        </header>

        <main>
            {/* NEW MESSAGE CARD */}
            <div className="message-card">
            <h3 style={{ margin: "0 0 10px", fontSize: "16px", fontWeight: 600 }}>Compose Message</h3>

            <textarea
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                placeholder="Write your message..."
            />

            {/* Attachments */}
            <div className="attachments">
                {attachments.map((file, index) => {
                const isImage = file.type.startsWith("image/");
                const previewUrl = isImage ? URL.createObjectURL(file) : null;
                return (
                    <div key={index} className="file-item">
                    {isImage && <img src={previewUrl!} alt={file.name} />}
                    <span>{file.name}</span>
                    <button className="file-remove" onClick={() => handleRemove(index)}>
                        <Trash2 size={14} />
                    </button>
                    </div>
                );
                })}
            </div>

            {/* Actions */}
            <div className="message-actions">
                <button onClick={() => fileInputRef.current?.click()}>
                <Paperclip size={14} /> Attach Files
                </button>
                <input
                type="file"
                ref={fileInputRef}
                multiple
                onChange={handleAttach}
                style={{ display: "none" }}
                />

    <button
    className="primary-btn"
    onClick={handleSend}
    disabled={sending}
    style={{
        opacity: sending ? 0.8 : 1,
        cursor: sending ? "not-allowed" : "pointer",
        display: "inline-flex",
        alignItems: "center",
        justifyContent: "center",
        gap: "6px",
        minWidth: "130px",
        position: "relative",
    }}
    >
    {sending ? (
        <>
        <span
            style={{
            width: "14px",
            height: "14px",
            border: "2px solid rgba(255,255,255,0.4)",
            borderTopColor: "white",
            borderRadius: "50%",
            display: "inline-block",
            animation: "spin 0.8s linear infinite",
            }}
        ></span>
        Sending...
        <style>{`
            @keyframes spin {
            from { transform: rotate(0deg); }
            to { transform: rotate(360deg); }
            }
        `}</style>
        </>
    ) : (
        <>
        <Send size={16} />
        Send Message
        </>
    )}
    </button>

            </div>
            </div>
        </main>

              <footer>Tinh Hoa Viet</footer>
        </div>
    );
    };

    export default NewMessagePage;
